.my-pc-renderer-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.my-pc-renderer-container-2 {
	display: flex;
	flex-direction: column;
	width: 90%;
	background-color: #cbcbcb;
	border: 1px outset #b9b9b9;
	box-shadow: inset 1px 1px 1px 1px #e7e7e7;
}

.my-pc-info-father-container {
	margin: 20px;
}

.my-pc-info-container {
	display: flex;
	justify-content: center;
	align-items: center;
	column-gap: 10px;
}

.my-pc-info-container-2 {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.my-pc-title {
	font-size: 20px;
	font-weight: bold;
	font-family: "Silkscreen", sans-serif;
}

.my-pc-renderer-title {
	font-size: 30px;
	text-align: right;
	font-family: "Silkscreen", sans-serif;
	font-weight: 400;
}

.my-pc-renderer-title-container {
	display: flex;
	width: 90%;
}

.my-pc-title2-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: right;
}

.my-pc-title-2 {
	font-size: 15px;
	font-weight: bold;
	font-family: "Silkscreen", sans-serif;
}

.my-pc-config-button-container {
	display: flex;
	justify-content: right;
	width: 90%;
}

/* Responsividade para mobile */
@media (max-width: 768px) {
	.my-pc-renderer-container {
		padding: 10px;
	}

	.my-pc-renderer-title {
		font-size: 20px !important;
	}

	.my-pc-title {
		font-size: 16px !important;
	}

	.my-pc-info-container {
		flex-direction: column;
		row-gap: 15px;
		text-align: center;
	}

	.my-pc-info-container img {
		width: 100px !important;
		height: 100px !important;
	}

	.my-pc-title2-container {
		align-items: center !important;
		text-align: center;
	}

	.my-pc-info-container-2 {
		flex-direction: column;
		align-items: center;
		text-align: center;
		margin-top: 15px;
	}

	.my-pc-info-father-container {
		margin: 15px !important;
	}

	.my-pc-renderer-container-2 {
		width: 95% !important;
	}

	.my-pc-renderer-title-container {
		width: 95% !important;
	}

	.my-pc-config-button-container {
		width: 95% !important;
		justify-content: center;
		margin-top: 10px;
	}
}
